<template>
  <div v-show="isLocal" class="connect-type" @click="toggle()">Localhost</div>
  <div v-show="!isLocal" class="connect-type" @click="toggle()">Ngrok</div>

  <div v-if="isBigImg" class="img-size" @click="toggleSize()">Big image</div>
  <div v-else class="img-size" @click="toggleSize()">Small image</div>

  <div class="row">
    <div class="col" v-on:click="onRestore">Restore</div>
    <div class="col" v-on:click="reset">Refresh</div>
  </div>
  <div class="hello">
    <img :src="imageUrl" alt="Image description" />
  </div>
  <div class="row">
    <div class="col" v-on:mousedown="onAccept" @mouseup="stopRun">Accept</div>
    <div class="col" v-on:click="onReject">Reject</div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "App",
  data() {
    return {
      fileName: "",
      imageUrl: "",
      isLocal: true,
      isBigImg: true,
      isRun: false,
    };
  },
  computed: {
    baseUrl() {
      return this.isLocal ? "http://localhost:5000" : window.__baseUrl;
    },
  },
  created() {
    this.getNotAcceptedImage();
  },

  methods: {
    sleep(ms) {
      return new Promise((resolve) => setTimeout(resolve, ms));
    },
    toggleSize() {
      this.isBigImg = !this.isBigImg;
    },
    toggle() {
      this.isLocal = !this.isLocal;
    },
    async onRestore() {
      await axios.post(this.baseUrl + "/unverify-last-img");
      this.reset();
    },
    reset() {
      this.fileName = "";
      this.imageUrl = "";
      this.getNotAcceptedImage();
    },
    async onAccept() {
      this.isRun = true;
      while (this.isRun) {
        await axios.post(this.baseUrl + "/accept-img/" + this.fileName);
        this.reset();
        await this.sleep(200);
      }
    },
    stopRun() {
      this.isRun = false;
    },
    async onReject() {
      await axios.post(this.baseUrl + "/reject-img/" + this.fileName);
      this.reset();
    },
    async getNotAcceptedImage() {
      const res = await axios.post(this.baseUrl + "/get-first-image-to-verify");
      console.log(res);
      if (res.status == 200 && res.data.Data.length) {
        this.imageUrl =
          this.baseUrl +
          (this.isBigImg ? "/get-image/" : "/get-sml-image/") +
          res.data.Data[0].file_name;

        this.fileName = res.data.Data[0].file_name;
      }
    },
  },
};
</script>

